$page-border : 1px solid rgb(122, 114, 114);
$blue-color: #002B7A;
$blue-background-color : #000C29;

@mixin main-font-style {
    font-family: 'Rajdhani', sans-serif;
    color: rgb(122, 114, 114);
    font-size: 1.2rem;
}

@mixin title-font-style{
    //font-family: 'Capriola', sans-serif;
    font-family: 'Poiret One', sans-serif;
    font-size: 3rem;
    color: rgb(122, 114, 114);
    font-weight: 600;
}

@mixin title2-font-style{
    //font-family: 'Barlow Condensed', sans-serif;
    font-family: 'Source Serif 4', serif;
    font-size: 2rem;
    font-weight: 400;
    font-style: italic;
    color: #002B7A;
   //color: rgb(88, 81, 81);
}

@mixin title3-font-style{
    font-family: 'Capriola', sans-serif;
    font-size: 2rem;
    font-weight: 400;
    color: #F1EBE4
}


@mixin link-no-style {
    text-decoration: none;
    cursor: pointer;
    color :rgb(122, 114, 114);
    cursor: pointer;
}

@mixin link-bottom-style {
    position: relative;
  &:hover::before {
    width: 100%; // étendre la barre de soulignement sur toute la largeur
}

&::before {
    content: "";
    position: absolute;
    bottom: 0; // positionner en bas de .p1
    left: 50%; // positionner au centre horizontalement
    transform: translateX(-50%); // centrage horizontal
    height: 2px; // épaisseur de la barre de soulignement
    width: 0; // largeur initiale de 0
    background-color: rgb(122, 114, 114); // couleur de la barre de soulignement
    transition: width 0.3s ease-in-out; // animation de la largeur
}
}

@mixin button-style {
    border: rgb(122, 114, 114) double 3px;
    font-family: 'Rajdhani', sans-serif;
    color:rgb(122, 114, 114);
    font-size: 1.3rem;
    border-radius: 7px;
    padding: 0.5rem 1rem;
    background-color: transparent;
    cursor: pointer;
    transition: transform 0.3s ease-in-out;
    &:hover {
        transform: scale(1.1);
    }
}
